@use "global" as *;

.gallery-list {
  display: grid;
  gap: rem(10);
  @include mq(md) {
      grid-template-columns: repeat(9, 1fr);
      grid-auto-flow: dense;
  }
}

.gallery__list {
  margin-top: rem(40);

  @include mq(md) {
    margin-top: rem(80);
  }
}

.gallery-list__item {
  @include mq(md) {
      grid-column: span 5;
      grid-row: span 1;
  }
}

.gallery-list__item:nth-of-type(6n+1),
.gallery-list__item:nth-of-type(6n+6) {
  @include mq(md) {
      grid-column: span 4;
      grid-row: span 2;
  }
}

.gallery-list__item img {
  width: 100%;
  height: 100%;
  aspect-ratio: 580/369;
  object-fit: cover;
}

.gallery-list__item:nth-of-type(6n+1) img,
.gallery-list__item:nth-of-type(6n+6) img {
  aspect-ratio: 345/523;

  @include mq(md) {
    aspect-ratio: 580/369;
  }
}