.accordion {
  position: relative;
}

.accordion::before {
  background-image: url(../images/common/voice3.1.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  content: "";
  max-width: 7.5rem;
  height: 3.4375rem;
  top: -1.5375rem;
  left: calc(48% - min(44vw, 36rem));
  position: absolute;
  width: 100%;
  z-index: -1;
}
@media screen and (min-width: 768px) {
  .accordion::before {
    max-width: 18.8125rem;
    height: 8.625rem;
    top: -4.3375rem;
    left: calc(47% - min(44vw, 36rem));
  }
}

.accordion__inner {
  max-width: 37.5rem;
  width: 100%;
  margin-inline: auto;
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
}
@media screen and (min-width: 768px) {
  .accordion__inner {
    max-width: 46.875rem;
    padding-left: 25px;
    padding-right: 25px;
  }
}

.accordion__wrap + .accordion__wrap {
  margin-top: 2.4rem;
}
@media screen and (min-width: 768px) {
  .accordion__wrap + .accordion__wrap {
    margin-top: 2.1rem;
  }
}

.accordion__content {
  display: none;
}

.accordion__header {
  background-color: #408F95;
  color: #fff;
  font-size: 1rem;
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 400;
  padding: 0.9rem;
  transition: background 0.3s ease;
  cursor: pointer;
  position: relative;
}
@media screen and (min-width: 768px) {
  .accordion__header {
    font-size: 1.25rem;
    padding: 1.5625rem 2.5rem;
  }
}

.accordion__header::before,
.accordion__header::after {
  position: absolute;
  content: "";
  top: 0.0625rem;
  right: 1.25rem;
  bottom: 0;
  width: 1.75rem;
  height: 0.0625rem;
  margin: auto;
  background: #fff;
}
@media screen and (min-width: 768px) {
  .accordion__header::before,
  .accordion__header::after {
    width: 2rem;
    right: 2.25rem;
